const URL_LogoutUser =
  document.URL.indexOf("limroof.github.io") === -1
    ? process.env.REACT_APP_LOCAL_API_URL + "/users/logout/"
    : process.env.REACT_APP_CLOUD_API_URL + "/users/logout/";

export const logoutUser = (setIsAuthenticated) => {
  const header = {
    headers: {
      Accept: "application/json",
      "Content-Type": "application/json",
    },
    method: "GET",
    mode: "cors",
    credentials: "include",
  };
  fetch(URL_LogoutUser, header)
    .then((res) => res.json())
    .then((data) => {
      if (data.user.username === "") {
        setIsAuthenticated({ isAuthenticated: false, username: "" });
        localStorage.removeItem("isAuthenticated");
      } else {
      }
    })
    .catch((e) => {});
};
