import React from "react";
import {
  Headerh,
  TopBar,
  Nav,
  MobileNav,
  ImgLogo,
  StyledLink,
  Drawer,
  Burger,
  CloseIcon,
} from "./Styles/StylesHeader";
import menuIcon from "../../public/Assets/img/menu-icon.svg";
import closeIcon from "../../public/Assets/img/close-icon.svg";
import gh_icon from "../../public/Assets/img/gh_icon.svg";

import { useEffect, useState } from "react";
import { useContext } from "react";
import { UserContext } from "../../Pages";
import { logoutUser } from "../../api/Logout";

export default function Header() {
  const [isDrawerOpen, setDrawer] = useState(false);
  const { isAuthenticated, setIsAuthenticated } = useContext(UserContext);

  useEffect(() => {
    document.getElementById("topBar").classList.add("--active");
  }, []);

  const openDrawer = () => {
    document.querySelector("body").classList.add("--no-scroll");
    setDrawer(true);
  };

  const closeDrawer = () => {
    document.getElementById("topBar").classList.remove("--active--mobile");
    document.querySelector("body").classList.remove("--no-scroll");
    document.querySelectorAll(".AnimateOpen").forEach((elem) => {
      elem.classList.add("AnimateClose");
    });
    document.getElementById("drawer").classList.add("--closing");
    setTimeout(() => {
      setDrawer(false);
    }, 2000);
  };

  return (
    <Headerh>
      <TopBar id="topBar">Now available in French!</TopBar>
      <Nav>
        <StyledLink to="/">
          <ImgLogo />
        </StyledLink>
        <StyledLink to="/why-coding">What is Coding</StyledLink>
        <StyledLink to="/curriculum/fundamentals-1">Curriculum</StyledLink>
        <StyledLink to="/glossary/data-types">Glossary</StyledLink>
        <StyledLink to="/about">About Us</StyledLink>
        <StyledLink to="/faq">FAQ</StyledLink>
        {!isAuthenticated.isAuthenticated ? (
          <StyledLink to="/signin" className="AccountLink">
            Mon compte
          </StyledLink>
        ) : (
          <div className="AccountLink">
            <StyledLink to="/account">
              Bonjour {isAuthenticated.username}
            </StyledLink>
            <StyledLink onClick={() => logoutUser(setIsAuthenticated)}>
              Déconnexion
            </StyledLink>
          </div>
        )}
      </Nav>

      <MobileNav>
        <StyledLink to="/" className="logoMobile">
          <ImgLogo />
        </StyledLink>
        {isDrawerOpen && (
          <Drawer id="drawer">
            <CloseIcon
              onClick={() => {
                closeDrawer();
              }}
            >
              <img src={closeIcon} alt={menuIcon}></img>
            </CloseIcon>

            <StyledLink
              to="/"
              className="AnimateOpen"
              onClick={() => {
                closeDrawer();
              }}
            >
              <img src={gh_icon} alt={gh_icon}></img>
            </StyledLink>
            {!isAuthenticated.isAuthenticated ? (
              <StyledLink
                to="/signin"
                className="AccountLink AnimateOpen"
                onClick={() => {
                  closeDrawer();
                }}
              >
                Mon compte
              </StyledLink>
            ) : (
              <div className="AccountLink AnimateOpen">
                <StyledLink
                  to="/account"
                  onClick={() => {
                    closeDrawer();
                  }}
                >
                  Bonjour {isAuthenticated.username}
                </StyledLink>
                <StyledLink
                  onClick={() => {
                    closeDrawer();
                    logoutUser(setIsAuthenticated);
                  }}
                >
                  Déconnexion
                </StyledLink>
              </div>
            )}
            <StyledLink
              to="/why-coding"
              className="AnimateOpen"
              onClick={() => {
                closeDrawer();
              }}
            >
              What is Coding
            </StyledLink>
            <StyledLink
              to="/curriculum/fundamentals-1"
              className="AnimateOpen"
              onClick={() => {
                closeDrawer();
              }}
            >
              Curriculum
            </StyledLink>
            <StyledLink
              to="/glossary/data-types"
              className="AnimateOpen"
              onClick={() => {
                closeDrawer();
              }}
            >
              Glossary
            </StyledLink>
            <StyledLink
              to="/about"
              className="AnimateOpen"
              onClick={() => {
                closeDrawer();
              }}
            >
              About Us
            </StyledLink>
            <StyledLink
              to="faq"
              className="AnimateOpen"
              onClick={() => {
                closeDrawer();
              }}
            >
              FAQ
            </StyledLink>
            <button className="cta-button start-coding">Get the app</button>
          </Drawer>
        )}

        <Burger
          onClick={() => {
            openDrawer();
          }}
        >
          <img src={menuIcon} alt={menuIcon}></img>
        </Burger>
      </MobileNav>
    </Headerh>
  );
}
