import styled from "styled-components";
import { Link } from "react-router-dom";
import logoImg from "../../../public/Assets/img/logo.blue.svg";
import logoTablette from "../../../public/Assets/img/demo__logo-grasshopper-lockup.svg";
import { keyframes } from "styled-components";

export const Headerh = styled.header`
  position: sticky;
  top: 40px;
  z-index: 100;
  background: #fcfcfc;
`;
export const TopBar = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  position: relative;
  z-index: 100;
  background-color: #45c14e;
  color: white;
  font-size: 13px;
  font-weight: 500;
  transform: translateY(-100%);
  transition: transform 500ms ease;
  &.--active {
    transform: translateY(0%);
  }
  position: fixed;
  top: 0;
`;

export const Nav = styled.nav`
  height: 100px;
  display: flex;
  column-gap: 40px;
  align-items: center;
  padding: 0 40px;
  @media (max-width: 960px) {
    display: none;
  }
  .AccountLink {
    margin-left: auto;
    position: relative;
    a:nth-child(2) {
      position: absolute;
      top: 25px;
      left: 0;
      max-height: 0px;
      overflow: hidden;
      transition: 0.3s max-height ease-out;
    }
    &:hover :nth-child(2) {
      max-height: 100px;
    }
  }
`;
export const ImgLogo = styled.div`
  background-image: url(${logoImg});
  background-size: 100%;
  background-repeat: no-repeat;
  background-position: center;
  width: 163px;
  height: 28px;
  @media (max-width: 717px) {
    background-image: url(${logoTablette});
    width: 128px;
    height: 28px;
  }
`;
const openLink = keyframes`
 0% {
  opacity:0;
  transform: translatex(65%);
}
 100% {
  opacity:1;
  transform: translatex(0%);
}
`;
const openLogo = keyframes`
 0% {
  opacity:0;
  transform: translatex(-65%);
}
 100% {
  opacity:1;
  transform: translatex(0%);
}
`;

const closeLink = keyframes`
 0% {
  opacity:1;
  transform: translatex(0%);
}
 100% {
  opacity:0;
  transform: translatex(65%);
}
`;
const closeLogo = keyframes`
 0% {
  opacity:1;
  transform: translatex(0%);
}
 100% {
  opacity:0;
  transform: translatex(-65%);
}
`;

export const StyledLink = styled(Link)`
  color: #99adb7;
  font-weight: 500;
  display: flex;
  width: fit-content;
  position: relative;
  &:hover {
    color: #002c43;
  }
  &.Signin {
    position: absolute;
    right: 40px;
  }
  @media (max-width: 960px) {
    &:not(.logoMobile) {
      margin-bottom: 50px;
    }
    font-size: 28px;
    animation: ${openLink} 0.5s forwards ease-out;
    &.AnimateOpen {
      opacity: 0;
    }
    &.StyledLink {
      display: none;
    }
    &.AnimateOpen:nth-child(2) {
      margin-bottom: 70px;
      animation-name: ${openLogo};
      animation-delay: 0.5s;
    }
    &.AnimateOpen:nth-child(3) {
      animation-delay: 0.5s;
    }
    &.AnimateOpen:nth-child(4) {
      animation-delay: 0.75s;
    }
    &.AnimateOpen:nth-child(5) {
      animation-delay: 1s;
    }
    &.AnimateOpen:nth-child(6) {
      animation-delay: 1.25s;
    }
    &.AnimateOpen:nth-child(7) {
      animation-delay: 1.5s;
    }
    &.AnimateOpen:nth-child(8) {
      animation-delay: 1.75s;
    }
    &.AnimateClose {
      animation: ${closeLink} 0.5s forwards;
      opacity: 1;
    }
    &.AnimateClose:nth-child(2) {
      animation-name: ${closeLogo};
      animation-delay: 0s;
    }
    &.AnimateClose:nth-child(3) {
      animation-delay: 0s;
    }
    &.AnimateClose:nth-child(4) {
      animation-delay: 0.25s;
    }
    &.AnimateClose:nth-child(5) {
      animation-delay: 0.5s;
    }
    &.AnimateClose:nth-child(6) {
      animation-delay: 0.75s;
    }
    &.AnimateClose:nth-child(7) {
      animation-delay: 1s;
    }
    &.AnimateClose:nth-child(8) {
      animation-delay: 1.25s;
    }
  }
`;

export const MobileNav = styled.nav`
  display:none;
  height: 100px;
  align-items: center;
  justify-content: space-between;
  padding-left: 40px;
  padding-right: 40px;
  position:relative;
  @media (max-width: 960px) {
    display:flex;
  }
  @media (max-width: 717px) {
    height: 60px;
    padding-left: 20px;
    padding-right: 20px;
  }

  div.AnimateOpen {
    animation: ${openLink} 0.5s forwards;
    opacity: 0;
    animation-delay: 0.5s;
  }
  div.AnimateClose{
    animation: ${closeLink} 0.5s forwards;
    opacity: 1;
    animation-delay: 0s;
  }
  div.AnimateOpen a:nth-child(1) {
    margin-bottom:30px;
  }
   div.AnimateOpen a:nth-child(2) {
    padding-left: 40px;
    font-size: 25px;
  }
}
`;

const openDrawer = keyframes`
 0% { transform: translatex(-150%); }
 100% { transform: translatex(0%); }
`;
const closeDrawer = keyframes`
 0% { transform: translatex(0%); }
 100% { transform: translatex(-150%); }
`;

export const Drawer = styled.div`
  height: calc(100vh - 40px);
  width: 100%;
  flex-direction: column;
  position: fixed;
  top: 40px;
  left: 0;
  background-color: #00324b;
  z-index: 100;
  transform: translatex(-150%);
  padding: 20px;
  overflow: hidden;
  animation: ${openDrawer} 0.5s forwards;
  &.--closing {
    transform: translatex(0%);
    animation: ${closeDrawer} 0.5s forwards;
    animation-delay: 1.5s;
  }
`;

export const Burger = styled.div`
  cursor: pointer;
  position: absolute;
  top: 18px;
  right: 20px;
  span {
    font-size: 32px;
    font-weight: bold;
  }
`;
export const CloseIcon = styled.div`
  cursor: pointer;
  position: absolute;
  top: 18px;
  right: 20px;
  span {
    font-size: 32px;
    font-weight: bold;
  }
`;
