import {
  Container,
  H1,
  SigninForm,
  Form,
  Input,
  Button,
} from "./Styles/Signin";
import { Link, Navigate } from "react-router-dom";
import spinner from "../../public/Assets/img/spinner_white.png";
import { loginUser } from "../../api/loginUser";

import React, { useState } from "react";
import { UserContext } from "../../Pages/index";
import { useContext } from "react";

export default function Signin() {
  const { isAuthenticated, setIsAuthenticated } = useContext(UserContext);
  const [isLoading, setIsLoading] = useState(false);
  const [isFormSuccessful, setIsFormSuccessful] = useState(true);
  const [errorMsg, setErrorMsg] = useState(
    "Paire d'identifiant/mot de passe est incorrecte"
  );

  let handleForm = async (e) => {
    e.preventDefault();
    setIsLoading(true);
    const formDataObj = {};
    const myFormData = new FormData(e.target);
    myFormData.forEach((value, key) => (formDataObj[key] = value));

    loginUser(
      formDataObj,
      setIsLoading,
      setIsFormSuccessful,
      setErrorMsg,
      setIsAuthenticated
    );
  };
  /*   useEffect(() => {
    document.querySelectorAll(".addClass").forEach((elem) => {
      elem.classList.add("--active");
    });
  }, []); */

  return (
    <React.Fragment>
      {isAuthenticated.isAuthenticated && <Navigate to="/account" />}
      <Container className="App">
        <SigninForm className="addClass">
          <div>
            <H1>Connexion</H1>
            <Form onSubmit={(e) => handleForm(e)} autoComplete="false">
              <Input>
                <span className="material-symbols-outlined material-icons">
                  badge
                </span>
                <input
                  type="text"
                  name="username"
                  placeholder="Identifiant"
                  required
                ></input>
              </Input>
              <Input>
                <span className="material-symbols-outlined material-icons">
                  badge
                </span>
                <input
                  type="password"
                  name="password"
                  placeholder="Mot de passe"
                  required
                ></input>
              </Input>
              <Input className="signup-pswd">
                <Link to="/signup" className="signup">
                  <small>Vous n'avez pas encore de compte?</small>
                </Link>
                <Link to="/account" className="reset-password">
                  <small>Mot de passe oublié?</small>
                </Link>
              </Input>
              <Button
                type="submit"
                className="cta-button start-coding"
                disabled={!isLoading ? false : true}
              >
                {!isLoading ? (
                  "Se connecter"
                ) : (
                  <img
                    src={spinner}
                    className="loading spinner"
                    alt="loading"
                  ></img>
                )}
              </Button>

              {!isFormSuccessful && (
                <small className="errmsg">{errorMsg}</small>
              )}
            </Form>
          </div>
        </SigninForm>
      </Container>
    </React.Fragment>
  );
}
