import React from "react";
import Home from "../Pages/Home/Home";
import Signup from "../Pages/Signup/Signup";
import Login from "./Signin/Login.jsx";
import AccountPage from "../Pages/AccountPage/AccountPage.jsx";

import Header from "../Component/Header/Header";
import Footer from "../Component/Footer/Footer";
import {
  /* BrowserRouter,  */ HashRouter,
  Routes,
  Route,
} from "react-router-dom";
import { useState, createContext } from "react";
import { useEffect } from "react";

export const UserContext = createContext();

export default function Index() {
  const [isAuthenticated, setIsAuthenticated] = useState({
    isAuthenticated: false,
    username: "",
  });
  useEffect(() => {
    const loggedUser = JSON.parse(localStorage.getItem("isAuthenticated"));
    loggedUser &&
      localStorage.getItem("isAuthenticated") &&
      setIsAuthenticated({
        isAuthenticated: loggedUser.isAuthenticated,
        username: loggedUser.username,
      });
  }, []);
  return (
    <HashRouter basename="/">
      <UserContext.Provider value={{ isAuthenticated, setIsAuthenticated }}>
        <Header />
        <Routes>
          <Route path="/" element={<Home />} title="Ismailos" />
          <Route path="/signup" element={<Signup />} title="Ismailos" />
          <Route path="/signin" element={<Login />} title="Ismailos" />
          <Route path="/account" element={<AccountPage />} title="Ismailos" />
        </Routes>
        <Footer />
      </UserContext.Provider>
    </HashRouter>
  );
}
