import styled from "styled-components";
import { keyframes } from "styled-components";

export const Container = styled.section`
  width: 100%;
  display: flex;
  height: calc(100vh - 40px - 100px);
  min-height: 500px;
  max-height: 1000px;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 10px;
  overflow: hidden;
`;
export const Banner = styled.div`
  background-image: linear-gradient(45deg, #3f51b5, #9b00e8);
  display: flex;
  justify-content: center;
  align-items: center;
  transition-property: border, witdh;
  width: 100%;
  &.active {
    border-top-right-radius: 7%;
    border-bottom-right-radius: 7%;
    width: 40%;
    min-width: 400px;
    width: 40%;
  }
`;
export const H1 = styled.h1`
  color: #002c43;
  font-size: 36px;
  font-weight: 500;
  margin-bottom: 20px;
  margin-top: 0;
  line-height: 46px;
  text-align: center;
  @media (max-width: 700px) {
    font-size: 30px;
    max-width: 320px;
    line-height: 36px;
    margin-bottom: 40px;
  }
`;

export const SigninForm = styled.div`
  display: flex;
  justify-content: center;
  flex-direction: column;
  overflow: hidden;
  width: 100%;
  min-height: 100%;
  align-items: center;

  @media (max-width: 1200px) {
    width: 100%;
    padding-top: 40px;
  }
  @media (max-width: 700px) {
    justify-content: flex-start;
    padding-left: 0px;
    padding-right: 0px;
  }
`;

export const Form = styled.form`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  row-gap: 20px;
  align-items: center;
  .signup,
  .reset-password {
    width: fit-content;
    align-self: flex-start;
  }
  .signup:hover,
  .reset-password:hover {
    color: #002c43c4;
  }
  .errmsg {
    margin-top: -15px;
    width: 100%;
    max-width: 280px;
    text-align: center;
    color: #e91e63;
  }
`;

export const Input = styled.div`
  position: relative;
  width: 100%;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  > input {
    border: none;
    height: 100%;
    width: 100%;
    padding-left: 50px;
    padding-right: 20px;
    min-height: 40px;
    border-radius: 5px;
    box-shadow: 0px 10px 49px -19px rgb(0 0 0 / 70%);
    border: 1px solid rgb(69 193 78 / 19%);
    font-size: 18px;
  }
  span {
    position: absolute;
    left: 15px;
    pointer-events: none;
  }

  &.signup-pswd {
    margin-top: -15px;
    justify-content: space-between;
    column-gap: 30px;
  }
`;

const Loading = keyframes`
    0% {
    transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
`;

export const Button = styled.button`
  height: 54px;
  width: 100%;
  &:active {
    transform: scale(0.98);
  }
  &:hover {
    opacity: 0.92;
  }
  }
`;
