/* const URL_SaveUser = "https://ismail-two.vercel.app/users/register"; */
const URL_SaveUser =
  document.URL.indexOf("limroof.github.io") === -1
    ? process.env.REACT_APP_LOCAL_API_URL + "/users/register/"
    : process.env.REACT_APP_CLOUD_API_URL + "/users/register/";

export const saveUser = (
  newUser,
  setIsLoading,
  setIsFormSuccessful,
  setErrorMsg,
  setIsAuthenticated
) => {
  const header = {
    headers: {
      Accept: "application/json",
      "Content-Type": "application/json",
    },
    credentials: "include",
    method: "POST",
    body: JSON.stringify(newUser),
  };
  fetch(URL_SaveUser, header)
    .then((res) => res.json())
    .then((data) => {
      if (data.ok) {
        setErrorMsg("Inscription réussie");
        setIsFormSuccessful(+true);
        setIsAuthenticated({
          isAuthenticated: true,
          username: data.username,
        });
      } else {
        console.error("error");
        setIsFormSuccessful(+false);
        setIsLoading(+false);
        setErrorMsg(data.message);
      }
    })
    .catch((e) => {
      console.error(e);
      setIsLoading(+false);
      setIsFormSuccessful(+false);
    });
};
