import React from "react";
import { useState, useEffect } from "react";
import { getTodoAll } from "../../api/getTODO.js";
import { saveTodo } from "../../api/addTODO.js";
import { updateTodo } from "../../api/updateTodo.js";
import { deleteTodo } from "../../api/deleteTODO.js";
import spinnerWhite from "../../public/Assets/img/spinner_white.png";
import spinnerBlack from "../../public/Assets/img/spinner_black.png";

import {
  Container,
  Top,
  Input,
  Button,
  Table,
  Item,
  PlaceHolderLoading,
} from "./styles/HomePage.js";

export default function Todos() {
  const [listTodos, setListTodos] = useState();
  const [text, setText] = useState();
  const [activeChange, setActiveChange] = useState(false);
  const [current, setCurrent] = useState({});
  const [isLoading, setIsLoading] = useState(true);

  const addTodo = () => {
    setIsLoading(true);
    saveTodo(text, fetchAll, setIsLoading);
  };

  const fetchAll = () => {
    getTodoAll(setListTodos, setActiveChange, setIsLoading);
  };
  useEffect(() => {
    fetchAll();
  }, []);

  const hanleEdit = () => {
    setIsLoading(true);
    updateTodo(fetchAll, current, text, setText, setIsLoading); // passer l'id de l'element à changer + la mise à jour
  };
  const handleDelete = (current) => {
    setIsLoading(true);
    deleteTodo(fetchAll, current, setIsLoading);
  };

  return (
    <Container className="Tableau">
      <Top>
        <Input
          type="text"
          value={text}
          placeholder={
            !activeChange ? "Ajouter une tâche" : "Modifier une tâche"
          }
          onChange={(e) => {
            setText(e.target.value);
          }}
        ></Input>
        <Button
          className={`primary ${!activeChange ? `add` : `edit`}`}
          disabled={isLoading ? true : false}
          onClick={() => {
            setText("");
            !activeChange ? addTodo() : hanleEdit();
          }}
        >
          {!isLoading ? (
            !activeChange ? (
              "Ajouter"
            ) : (
              "Modifier"
            )
          ) : (
            <img
              src={spinnerWhite}
              className="loading spinner"
              alt="spinner"
            ></img>
          )}
        </Button>
        {activeChange && (
          <Button
            className="primary cancel"
            disabled={isLoading ? true : false}
            onClick={() => {
              setActiveChange(false);
              setText("");
            }}
          >
            Annuler
          </Button>
        )}
      </Top>
      {!isLoading ? (
        listTodos && listTodos.length > 0 ? (
          <Table>
            <tbody>
              {listTodos.map((elem, index) => {
                return (
                  <Item key={`${index}elllas`}>
                    <td colSpan="3">{elem.name}</td>
                    <td colSpan="1">
                      <button
                        onClick={() => {
                          handleDelete(elem);
                        }}
                      >
                        <span className="material-symbols-outlined material-icons">
                          delete_sweep
                        </span>
                      </button>
                    </td>
                    <td colSpan="1">
                      <button
                        onClick={() => {
                          setActiveChange(true);
                          setCurrent(elem);
                          setText(elem.name);
                        }}
                      >
                        <span className="material-symbols-outlined material-icons">
                          edit
                        </span>
                      </button>
                    </td>
                  </Item>
                );
              })}
            </tbody>
          </Table>
        ) : (
          <div>Aucune tâche trouvées</div>
        )
      ) : (
        <PlaceHolderLoading>
          <img
            src={spinnerBlack}
            className="loading spinner_black"
            alt="spinner loading"
          ></img>
        </PlaceHolderLoading>
      )}
    </Container>
  );
}
