const URL_getTodo =
  document.URL.indexOf("limroof.github.io") === -1
    ? process.env.REACT_APP_LOCAL_API_URL + "/users/todos/"
    : "https://ismail-two.vercel.app/users/todos/";

export const getTodoAll = (setListTodos, setActiveChange, setIsLoading) => {
  const header = {
    headers: {
      Accept: "application/json",
      "Content-Type": "application/json",
    },
    method: "GET",
    mode: "cors",
    credentials: "include",
  };
  fetch(URL_getTodo, header)
    .then((res) => res.json())
    .then((data) => {
      setActiveChange(false);
      setListTodos(data.todos);
      setIsLoading(false);
    })
    .catch((err) => {
      setIsLoading(false);
      console.error(err);
    });
};
